import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IHomeService } from '../@intermediate/home.interface';
import { GetHomeRequest, GetHomeResponse } from '../@intermediate/models/get-home.model';

@Injectable({
  providedIn: 'root'
})

export class HomeService implements IHomeService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Office/Dashboard/Home';
  }

  Get(request: GetHomeRequest): Observable<GetHomeResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetHomeResponse>(apiUrl, { params: request as any });
  }
}

