import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ResponsiveService } from '@tei/core';
import { Observable, Subject } from 'rxjs';
import { takeUntil, tap } from 'rxjs/operators';
import { IHomeService } from '../../@intermediate/home.interface';
import { GetHomeRequest, GetHomeResponse } from '../../@intermediate/models/get-home.model';

@Component({
  selector: 'office-dashboard-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, OnDestroy
{
  private destroy$ = new Subject();
  isSmallScreen: boolean = false;
  allNewsAnnouncementsXML: string;
  home$: Observable<GetHomeResponse> = this.service.Get(GetHomeRequest).pipe(
    tap(async res =>
    {
      if (res?.Feed?.URL)
      {
        try
        {
          const urlResponse = await fetch(res.Feed.URL);
          this.allNewsAnnouncementsXML = await urlResponse.text();
        } catch (error)
        {
          console.error('Failed to fetch feed data:', error);
        }
      }
    })
  );

  constructor(
    private responsiveService: ResponsiveService,
    @Inject('IHomeService') private service: IHomeService
  )
  {

  }

  ngOnInit(): void
  {
    this.detectMobileScreen();
  }

  private detectMobileScreen(): void
  {
    this.responsiveService.screenWidth
      .pipe(takeUntil(this.destroy$))
      .subscribe(screenWidth =>
      {
        this.isSmallScreen = screenWidth <= 768;
      });
  }

  ngOnDestroy()
  {
    this.destroy$.next(null);
    this.destroy$.complete();
  }
}
